@import '../../../styles/variables.scss';
@import '../../../styles/_mixins.scss';

@mixin tableColumnWidth() {
  &:nth-child(1) {
    width: 80px;
  }
  &:nth-child(2) {
    width: 140px;
  }
  &:nth-child(3) {
    width: 200px;
  }
}

.btn {
  position: relative;
  @include buttonBase;
  @include buttonStyle($primary, $white);
  &:before {
    content: '';
    display: block;
    position: absolute;
    width: 16px;
    height: 16px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
  }
  &__edit:before { background-image: url(../../../assets/images/edit-solid.svg);}
  &__new:before { background-image: url(../../../assets/images/plus.svg);}
}

.statusesTable {
  &__wrapper {
    height: 100%;
    overflow-y: auto;
  }
  &__pic {
    padding-top: #{'max(40%, 50px)'};
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
  }
  &__root {
    @include tableStyle;
    th {
      @include tableColumnWidth;
    }
    @media (max-width: $mobile) {
      width: 100%;
    }
  }
}
