@import '../../../styles/variables.scss';
@import '../../../styles/_mixins.scss';

@mixin tableColumnWidth() {
  &:nth-child(1) {
    width: 40px;
  }
  &:nth-child(2) {
    width: 160px;
  }
  &:nth-child(3),
  &:nth-child(8) {
    width: 120px;
  }
  &:nth-child(4),
  &:nth-child(5) {
    width: 90px;
  }
  &:nth-child(6) {
    width: 100px;
  }
  &:nth-child(7) {
    width: 220px;
  }
}

.carTable {
  &__wrapper {
    height: 100%;
    overflow-y: auto;
  }
  &__pic {
    padding-top: #{'max(40%, 50px)'};
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
  }
  &__root {
    width: 100%;
    @include tableStyle;
    th {
      @include tableColumnWidth;
    }
  }
}

.btnLink {
  position: relative;
  @include buttonBase;
  &:visited {
    color: $white;
    background-color: $primary;
  }
  @include buttonStyle($primary, $white);
  &:before {
    content: '';
    display: block;
    position: absolute;
    width: 16px;
    height: 16px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
  }
  &__edit:before { background-image: url(../../../assets/images/edit-solid.svg);}
  &__new:before { background-image: url(../../../assets/images/plus.svg);}
}
