@import './variables.scss';

@mixin buttonBase() {
  display: inline-block;
  min-width: 30px;
  height: 30px;
  margin: 0;
  font-size: 12px;
  line-height: 30px;
  vertical-align: middle;
  text-decoration: none;
  border: none;
  border-radius: 4px;
  transition: all ease 0.2s;
  &:active {
    box-shadow: -6px -10px 29px -5px rgba(43, 48, 52, 0.92) inset;
  }
  &:disabled {
    cursor: default;
  }
}

@mixin buttonStyle($bg-color, $color) {
  color: $color;
  background-color: $bg-color;
  &:visited {
    background-color: darken($bg-color, 10%);
  }
  &:hover {
    background-color: darken($bg-color, 5%);
  }
  &:disabled {
    background: $gray;
  }
}

@mixin menuElementHover() {
  &:hover {
    background-color: rgba($gray, 0.2);
  }
}

@mixin whiteBox() {
  background-color: $white;
  border-radius: 9px;
  box-shadow: 0px 1px 0px rgba(90, 97, 105, 0.11), 0px 2px 4px rgba(90, 97, 105, 0.12),
    0px 5px 5px rgba(90, 97, 105, 0.06), 0px 3.5px 35px rgba(90, 97, 105, 0.1);
}

@mixin visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

@mixin tableStyle {
  & {
    border-collapse: collapse;
    table-layout: fixed;
    thead {
      position: sticky;
      top: 0;
      z-index: 1;
      background-color: $white;
      box-shadow: inset 0 -1px 0 $border;
      tr {
        height: 60px;
      }
      th {
        padding: 5px 15px;
        // @include tableColumnWidth;
      }
    }

    tbody {
      tr {
        height: 60px;
        @include menuElementHover;
        transition: all ease 0.1s;
      }
      td {
        font-size: 15px;
        padding: 5px 15px;;
        text-align: center;
      }
    }
  }
}
